<template>
  <!-- Navigation -->
  <nav class="navbar navbar-expand-lg navbar-dark fixed-top" v-bind:class="{ 'navbar-shrink': scrollY }" id="mainNav" ref="mainNav">
    <div class="container">
      <a
        @click="$event.preventDefault()"
        class="navbar-brand js-scroll-trigger"
        href="#page-top"
        v-scroll-to="{
          el: '#mainNav',
          easing: 'easeInOutExpo',
          duration: 1000
        }">GeneWebs</a>
      <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
        Menu
        <i class="fas fa-bars"></i>
      </button>
      <div class="collapse navbar-collapse" id="navbarResponsive">
        <ul class="navbar-nav text-uppercase ml-auto">
          <li class="nav-item">
            <a
              @click="$event.preventDefault()"
              class="nav-link js-scroll-trigger"
              href="#services"
              v-scroll-to="{
                el: '#services',
                easing: 'easeInOutExpo',
                duration: 1000
              }">Services</a>
          </li>
          <li class="nav-item">
            <a
              @click="$event.preventDefault()"
              class="nav-link js-scroll-trigger"
              href="#portfolio"
              v-scroll-to="{
                el: '#portfolio',
                easing: 'easeInOutExpo',
                duration: 1000
              }">Portfolio</a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'NavBar',
  data () {
    return {
      scrollY: false,
    }
  },
  created() {
    window.addEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      if (window.scrollY > 100) {
        this.scrollY = true;
      } else {
        this.scrollY = false;
      }
    }
  },
}
</script>



