<template>
  <section class="bg-light" id="portfolio">
    <div class="container">
      <div class="row">
        <div class="col-lg-12 text-center">
          <h2 class="section-heading text-uppercase">Portfolio</h2>
        </div>
      </div>
      <div class="row">
        <div class="col-md-4 col-sm-6 portfolio-item" v-for="proj in projects">
          <a class="portfolio-link" data-toggle="modal" :href="'#port'+proj.id">
            <div class="portfolio-hover">
              <div class="portfolio-hover-content">
                <i class="fas fa-plus fa-3x"></i>
              </div>
            </div>
            <img class="img-fluid" :src="proj.img" alt="">
          </a>
          <div class="portfolio-caption">
            <h4>{{ proj.name }}</h4>
            <p class="text-muted">{{ proj.tech }}</p>
          </div>
        </div>
      </div>
    </div>
  </section>

</template>

<script>

export default {
  name: 'Portfolio',
  data () {
    return {
      projects: [
        {
          'id': 1,
          'name': '3GX Hulugan',
          'company': '3GX IT Solutions',
          'img': require('@/assets/img/portfolio/hulugan.png'),
          'description': 'An in-house web application for product loans in 3GX IT Solutions. It features all the step of loan applications from adding of customers, credit investigations, approval, monthly payment notifications.',
          'date': 'September 2016',
          'tech': 'Laravel, Jquery, Bootstrap, Mysql',
        },
        {
          'id': 2,
          'name': 'Biz Avenue',
          'company': 'Mergepoint Solutions',
          'img': require('@/assets/img/portfolio/bizave.png'),
          'description': 'A business directory website based on Naga City. It was built using Laravel Framework and Vue.js',
          'date': 'May 2017',
          'tech': 'Laravel, Vue.js, Bootstrap, Mysql',
        },
        {
          'id': 3,
          'name': 'Node.js API Middleware',
          'company': 'Mergepoint Solutions',
          'img': require('@/assets/img/portfolio/node.png'),
          'description': 'A node.js script used to sync data from different e-commerce APIs (SOAP/JSON). Fetches all data from these APIs from the last execution datatime to sync.',
          'date': 'November 2017',
          'tech': 'Node.js, Mongodb, node-cron'
        },
        {
          'id': 4,
          'name': 'Foodsento',
          'company': 'Mergepoint Solutions',
          'img': require('@/assets/img/portfolio/foodsento.png'),
          'description': 'A food delivery web and mobile application in Naga City. This was built using Laravel, Vue.js, and Ionic',
          'date': 'January 2018',
          'tech': 'Laravel, Vue.js, Bootstrap, Ionic',
        },
        {
          'id': 5,
          'name': 'Heegus',
          'company': 'Mergepoint Solutions',
          'img': require('@/assets/img/portfolio/heegus.png'),
          'description': 'A Service Provider and Freelancing web application built using Laravel and Jquery',
          'date': 'October 2018',
          'tech': 'Laravel, Jquery, Bootstrap',
        },
      ],
    }
  }
}
</script>



