<template>
  <!-- <div> -->
    <div class="portfolio-modal modal fade" :id="'port'+proj.id" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="close-modal" data-dismiss="modal">
            <div class="lr">
              <div class="rl"></div>
            </div>
          </div>
          <div class="container">
            <div class="row">
              <div class="col-lg-8 mx-auto">
                <div class="modal-body">
                  <!-- Project Details Go Here -->
                  <h2 class="text-uppercase">{{ proj.name }}</h2>
                  <p class="item-intro text-muted">{{ proj.company }}</p>
                  <img class="img-fluid d-block mx-auto" :src="proj.img" alt="">
                  <p>{{ proj.description }}</p>
                  <ul class="list-inline">
                    <li>Date: {{ proj.date }}</li>
                    <li>Tech stack: {{ proj.tech }}</li>
                  </ul>
                  <button class="btn btn-primary" data-dismiss="modal" type="button">
                    <i class="fas fa-times"></i>
                    Close Project</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  <!-- </div> -->

</template>

<script>
export default {
  name: 'PortfolioModal',
  props: ['proj'],
  data () {
    return {

    }
  },
  created() {
    console.log(this.projects);
  }
}
</script>



