<template>
  <div id="app">
    <NavBar></NavBar>
    <router-view/>
    <Footer></Footer>
    <PortfolioModal v-for="proj in projects" :proj="proj"></PortfolioModal>
  </div>
</template>

<script>
import '@/assets/vendor/fontawesome-free/css/all.css';
import PortfolioModal from '@/components/PortfolioModal';
import NavBar from '@/components/NavBar';
import Footer from '@/components/Footer';
import '@/assets/scss/main.scss';
export default {
  name: 'App',
  components: {
    NavBar, Footer, PortfolioModal
  },
  data() {
    return {
      projects: [
        {
          'id': 1,
          'name': '3GX Hulugan',
          'company': '3GX IT Solutions',
          'img': require('@/assets/img/portfolio/hulugan.png'),
          'description': 'An in-house web application for product loans in 3GX IT Solutions. It features all the step of loan applications from adding of customers, credit investigations, approval, monthly payment notifications.',
          'date': 'September 2016',
          'tech': 'Laravel, Jquery, Bootstrap, Mysql',
        },
        {
          'id': 2,
          'name': 'Biz Avenue',
          'company': 'Mergepoint Solutions',
          'img': require('@/assets/img/portfolio/bizave.png'),
          'description': 'A business directory website based on Naga City. It was built using Laravel Framework and Vue.js',
          'date': 'May 2017',
          'tech': 'Laravel, Vue.js, Bootstrap, Mysql',
        },
        {
          'id': 3,
          'name': 'Node.js API Middleware',
          'company': 'Mergepoint Solutions',
          'img': require('@/assets/img/portfolio/node.png'),
          'description': 'A node.js script used to sync data from different e-commerce APIs (SOAP/JSON). Fetches all data from these APIs from the last execution datatime to sync.',
          'date': 'November 2017',
          'tech': 'Node.js, Mongodb, Cron'
        },
      ],
    }
  }
}
</script>



